@font-face {
  font-family: Opensans;
  src: url("../fonts/opensans.woff");
  src: url("../fonts/opensans.woff2");
}
@font-face {
  font-family: Opensans-bold;
  src: url("../fonts/opensansbold.woff");
  src: url("../fonts/opensansbold.woff2");
}
.general-font {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
}
.title-font {
  font-family: "Opensans-bold", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}
.bold {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  background-color: white;
  height: 79px;
  display: flex;
}
.page-header__logo-image {
  margin-top: 14px;
  padding-left: 108px;
}
.main-nav__toggle {
  border: none;
  background-color: white;
  margin-top: 24px;
  margin-left: 61px;
}
.main-nav__wrapper {
  display: none;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle:hover .main.nav__wrapper {
  display: block;
}
.main-stock {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  min-height: 375px;
  background-position: center;
}
.main-stock__slogan {
  font-family: "Opensans-bold", sans-serif;
  font-size: 27px;
  font-weight: bold;
  line-height: 1.11;
  text-align: center;
  padding-top: 42px;
  padding-left: 39px;
  padding-right: 39px;
  color: #010000;
}
.main-stock__item {
  list-style: none;
  padding-left: 29px;
  padding-top: 30px;
  padding-bottom: 30px;
}
.main-stock__link {
  font-family: "Opensans-bold", sans-serif;
  font-size: 19px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.26;
  letter-spacing: normal;
  text-align: left;
  color: white;
  text-decoration: none;
}
.main-stock__item--toy {
  background-color: #56c3b2;
}
.main-stock__item--lamp {
  background-color: #63d1bb;
}
.promo__title {
  font-family: "Opensans-bold", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}
.promo__week {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.18;
  letter-spacing: normal;
  color: #63d1bb;
  text-align: left;
  text-transform: uppercase;
}
.promo__description {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
}
.feature-item__name {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
}
.feature-item__value {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
}
.promo__price {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
}
.promo__buy {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  line-height: 24px;
  padding: 20px 85px;
  background-color: white;
  border: solid 2px #444444;
  text-align: center;
}
.benefits__title {
  font-family: "Opensans-bold", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  text-align: left;
  color: #000000;
}
.benefits__item {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  list-style: none;
  padding: 0 0 85px 29px;
}
